.App {
  text-align: center;
}

.App-logo {
  height: 100vmin;
  pointer-events: none;
  width: 200px; 
  height: 200px; 
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 40px;
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-h3 {
  padding: 10px;
}

.App-h3,
.App-h5 {
  margin: 0;
}

.App-h5 {
  margin-top: 5px;
}

